@import url('https://fonts.googleapis.com/css?family=Shadows+Into+Light');


     .news-box{
         color:white;
         padding-right: 20px;
         text-align: center;
         padding-left: 20px;
         
         width: 50%;
         margin:0 auto;
        background: black;
        display:grid;
        grid-template-columns: repeat(1,1fr);
        overflow: auto;
     
        .news{
            font-family: 'Shadows Into Light', cursive;
            font-size: 24px;
            text-align: center;
            font-weight: 400;
         }
     }
