    background-color: white;
    margin: 6px 0;
    -webkit-transition: 0.4s;
    transition: 0.4s; }

.close {
  display: inline-block;
  cursor: pointer; }
  .close .bar1, .close .bar2, .close .bar3 {
    width: 35px;
    height: 5px;
    background-color: white;
    margin: 6px 0;
    -webkit-transition: 0.4s;
    transition: 0.4s; }
  .close .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px); }
  .close .bar2 {
    opacity: 0; }
